import React from 'react';

function SpellBook({ type, onClick, isSelected, isIndestructible }) {
  return (
    <div 
      className={`spell-book ${type} ${isSelected ? 'selected' : ''} ${isIndestructible ? 'indestructible' : ''}`} 
      onClick={onClick}
    >
      <img src={`/assets/books/${type}_book.png`} alt={type} />
    </div>
  );
}

export default SpellBook;