import { ELEMENTS, SPELL_THRESHOLD } from '../utils/gameConfig';

class SpellSystem {
  constructor() {
    this.spellProgress = {};
    ELEMENTS.forEach(element => {
      this.spellProgress[element] = 0;
    });
  }

  updateProgress(element, amount) {
    this.spellProgress[element] += amount;
    if (this.spellProgress[element] > SPELL_THRESHOLD) {
      this.spellProgress[element] = SPELL_THRESHOLD;
    }
  }

  canActivateSpell(element) {
    return this.spellProgress[element] >= SPELL_THRESHOLD;
  }

  activateSpell(element) {
    if (this.canActivateSpell(element)) {
      this.spellProgress[element] = 0;
      return true;
    }
    return false;
  }

  getSpellProgress() {
    return { ...this.spellProgress };
  }
}

export default SpellSystem;