import React, { useState, useEffect, useCallback } from 'react';
import SpellBook from './SpellBook';
import SpellSystem from './SpellSystem';
import { GRID_SIZE, INITIAL_MOVES, ELEMENTS } from '../utils/gameConfig';
import WebApp from '@twa-dev/sdk';

function GameBoard() {
  const [grid, setGrid] = useState([]);
  const [movesLeft, setMovesLeft] = useState(INITIAL_MOVES);
  const [score, setScore] = useState(0);
  const [spellSystem, setSpellSystem] = useState(null);
  const [selectedBook, setSelectedBook] = useState(null);

  useEffect(() => {
    initializeGrid();
    setSpellSystem(new SpellSystem());
  }, []);

  const initializeGrid = useCallback(() => {
    const newGrid = [];
    for (let y = 0; y < GRID_SIZE; y++) {
      newGrid[y] = [];
      for (let x = 0; x < GRID_SIZE; x++) {
        newGrid[y][x] = {
          type: ELEMENTS[Math.floor(Math.random() * ELEMENTS.length)],
          key: `${x}-${y}`
        };
      }
    }
    setGrid(newGrid);
  }, []);

  const handleBookClick = (x, y) => {
    if (selectedBook) {
      if (isAdjacent(selectedBook, { x, y })) {
        swapBooks(selectedBook, { x, y });
        setSelectedBook(null);
      } else {
        setSelectedBook({ x, y });
      }
    } else {
      setSelectedBook({ x, y });
    }
  };

  const isAdjacent = (pos1, pos2) => {
    const dx = Math.abs(pos1.x - pos2.x);
    const dy = Math.abs(pos1.y - pos2.y);
    return (dx === 1 && dy === 0) || (dx === 0 && dy === 1);
  };

  const swapBooks = (pos1, pos2) => {
    const newGrid = [...grid];
    const temp = newGrid[pos1.y][pos1.x];
    newGrid[pos1.y][pos1.x] = newGrid[pos2.y][pos2.x];
    newGrid[pos2.y][pos2.x] = temp;
    setGrid(newGrid);
    setMovesLeft(movesLeft - 1);
    checkMatches();
  };

  const checkMatches = () => {
    const newGrid = [...grid];
    let hasMatches = false;
    let matchedBooks = new Set();

    // Check horizontal matches
    for (let y = 0; y < GRID_SIZE; y++) {
      for (let x = 0; x < GRID_SIZE - 2; x++) {
        if (newGrid[y][x].type === newGrid[y][x+1].type && newGrid[y][x].type === newGrid[y][x+2].type) {
          matchedBooks.add(`${x},${y}`);
          matchedBooks.add(`${x+1},${y}`);
          matchedBooks.add(`${x+2},${y}`);
          hasMatches = true;
        }
      }
    }

    // Check vertical matches
    for (let x = 0; x < GRID_SIZE; x++) {
      for (let y = 0; y < GRID_SIZE - 2; y++) {
        if (newGrid[y][x].type === newGrid[y+1][x].type && newGrid[y][x].type === newGrid[y+2][x].type) {
          matchedBooks.add(`${x},${y}`);
          matchedBooks.add(`${x},${y+1}`);
          matchedBooks.add(`${x},${y+2}`);
          hasMatches = true;
        }
      }
    }

    if (hasMatches) {
      // Remove matched books and update score
      matchedBooks.forEach(coord => {
        const [x, y] = coord.split(',').map(Number);
        spellSystem.updateProgress(newGrid[y][x].type, 1);
        newGrid[y][x] = null;
      });
      setScore(score + matchedBooks.size * 10);

      // Refill the grid
      for (let x = 0; x < GRID_SIZE; x++) {
        let emptySpaces = 0;
        for (let y = GRID_SIZE - 1; y >= 0; y--) {
          if (newGrid[y][x] === null) {
            emptySpaces++;
          } else if (emptySpaces > 0) {
            newGrid[y + emptySpaces][x] = newGrid[y][x];
            newGrid[y][x] = null;
          }
        }
        for (let y = 0; y < emptySpaces; y++) {
          newGrid[y][x] = {
            type: ELEMENTS[Math.floor(Math.random() * ELEMENTS.length)],
            key: `${x}-${y}-new`
          };
        }
      }

      setGrid(newGrid);
      setTimeout(checkMatches, 500); // Check for new matches after refilling
    }
  };

  const handleSpellActivation = (element) => {
    if (spellSystem.canActivateSpell(element)) {
      spellSystem.activateSpell(element);
      applySpellEffect(element);
      checkMatches();
    }
  };

  const applySpellEffect = (element) => {
    const newGrid = [...grid];
    switch(element) {
      case 'fire':
        // Destroy a 3x3 area
        const centerX = Math.floor(Math.random() * (GRID_SIZE - 2)) + 1;
        const centerY = Math.floor(Math.random() * (GRID_SIZE - 2)) + 1;
        for (let y = centerY - 1; y <= centerY + 1; y++) {
          for (let x = centerX - 1; x <= centerX + 1; x++) {
            newGrid[y][x] = null;
          }
        }
        break;
      case 'water':
        // Shuffle bottom half of the grid
        for (let y = Math.floor(GRID_SIZE / 2); y < GRID_SIZE; y++) {
          for (let x = 0; x < GRID_SIZE; x++) {
            const randomX = Math.floor(Math.random() * GRID_SIZE);
            [newGrid[y][x], newGrid[y][randomX]] = [newGrid[y][randomX], newGrid[y][x]];
          }
        }
        break;
      case 'earth':
        // Make a random column indestructible for 3 turns
        const column = Math.floor(Math.random() * GRID_SIZE);
        for (let y = 0; y < GRID_SIZE; y++) {
          newGrid[y][column].indestructible = 3;
        }
        break;
      case 'air':
        // Swap two random columns
        const col1 = Math.floor(Math.random() * GRID_SIZE);
        let col2 = Math.floor(Math.random() * GRID_SIZE);
        while (col2 === col1) {
          col2 = Math.floor(Math.random() * GRID_SIZE);
        }
        for (let y = 0; y < GRID_SIZE; y++) {
          [newGrid[y][col1], newGrid[y][col2]] = [newGrid[y][col2], newGrid[y][col1]];
        }
        break;
    }
    setGrid(newGrid);
  };

  useEffect(() => {
    if (movesLeft === 0) {
      WebApp.showAlert(`Game Over! Your score: ${score}`);
      WebApp.MainButton.setText('Play Again').show().onClick(() => {
        setMovesLeft(INITIAL_MOVES);
        setScore(0);
        initializeGrid();
        setSpellSystem(new SpellSystem());
      });
    }
  }, [movesLeft, score, initializeGrid]);

  return (
    <div className="game-board">
      <div className="game-info">
        <span>Moves: {movesLeft}</span>
        <span>Score: {score}</span>
      </div>
      <div className="grid">
        {grid.map((row, y) => (
          <div key={y} className="row">
            {row.map((book, x) => (
              <SpellBook
                key={book.key}
                type={book.type}
                onClick={() => handleBookClick(x, y)}
                isSelected={selectedBook && selectedBook.x === x && selectedBook.y === y}
                isIndestructible={book.indestructible > 0}
              />
            ))}
          </div>
        ))}
      </div>
      <div className="spell-system">
        {ELEMENTS.map(element => (
          <button 
            key={element} 
            onClick={() => handleSpellActivation(element)}
            disabled={!spellSystem || !spellSystem.canActivateSpell(element)}
          >
            {element} Spell
          </button>
        ))}
      </div>
    </div>
  );
}

export default GameBoard;