import React, { useEffect, useState } from 'react';
import GameBoard from './components/GameBoard';
import WebApp from '@twa-dev/sdk';

function App() {
  const [isReady, setIsReady] = useState(false);
  const [user, setUser] = useState(null);

  useEffect(() => {
    setIsReady(true);
    setUser(WebApp.initDataUnsafe.user);

    WebApp.MainButton.setText('Restart Game').show().onClick(() => {
      // Здесь добавим логику перезапуска игры
    });

    WebApp.BackButton.show().onClick(() => {
      // Здесь добавим логику возврата в главное меню
    });

    return () => {
      WebApp.MainButton.offClick();
      WebApp.BackButton.offClick();
    };
  }, []);

  if (!isReady) {
    return <div>Loading...</div>;
  }

  return (
    <div className="app">
      <h1>Spellbook Puzzle</h1>
      {user && <p>Welcome, {user.first_name}!</p>}
      <GameBoard />
    </div>
  );
}

export default App;